.card-container {
  width: 31%;
  max-width: 30rem;
  height: 100%;
  padding: 1rem;
  border: 1px solid #888;
  background-color: white;
  border-radius: 0.4rem;
  box-shadow: 1px 1px 1px rgb(102, 101, 101);
}

.card-container p {
  font-size: 4rem;
  font-weight: bold;
  text-align: center;
  padding-top: 2rem;
}

.red {
  color: red;
}

.green {
  color: green;
}

@media (max-width: 880px) {
  .card-container p {
    font-size: 3rem;
  }
}
@media (max-width: 720px) {
  .card-container p {
    font-size: 2rem;
  }
}
@media (max-width: 550px) {
  .card-container h1 {
    font-size: 1.8rem;
  }
  .card-container p {
    font-size: 1.5rem;
  }
}
@media (max-width: 400px) {
  .card-container h1 {
    font-size: 1.5rem;
  }

  .card-container p {
    font-size: 1rem;
    padding-top: 1.5rem;
  }
}
